import React, { useState, useEffect } from 'react';
import { Link, Redirect } from 'react-router-dom';
// import CreateArticle from './CreateArticle';
import SignOut from './SignOut';
import '../css/App.css';


const Feed = (props) => {
    let { errorMessage, feed, signedIn, signOut, onLoad, onUpdate, onDelete } = props;
    const [data, setData] = useState([]);
    console.log(data, feed);

  const onEdit = (item) => {
    console.log('edit status', item, data);
    // let editState = Object.assign({}, data);
    let editState = data;
    let editted = editState.map(x => {
      x.editing = false;
      return x; 
    });
    let mapped = editState.map(x => {
      if(item.id === x.id) {
        x.editing = true;
        return x;
      }
      return x;
    });
    console.log(mapped, editted, data);
    setData({ data: mapped });
  }

  const onCancel = (item) => {
    console.log('cancel edit status');
    let editState = [...data];
    let mapped = editState.map(x => {
      if(item.id === x.id) {
        x.editing = false;
        return x;
      }
      return x;
    });
    console.log(mapped, data);
    setData({ data: mapped });
  }

  const handleChange = ({target}) => {
    let name = target.name;
    let val = target.value;
    let keyArray = ['title', 'article'];
    if (keyArray.indexOf(name) >= 0) {
    this.setState(prev => ({[name]: ''}));
    }
    console.log('name =>', name, ': value =>', val);
  
    if (name === 'undefined' || name === undefined) {
      console.log('name is undefined', 'target =>', target.parentNode, 'val =>', val);
      return;
    }
    if (val === '' || val.trim() === '') {
      target.classList.add('empty');
      console.log(`${name} is not supplied`);
      return;
    }
    this.setState(prev => ({ [name]: val }));
  }

  
    
    useEffect(() => {
      onLoad();
      setData(feed.data);
    }, [data]);



        //   let { errorMessage, feed, signedIn, signOut, onUpdate, onDelete } = props;
        if(errorMessage === 'Error with credentials') {
            console.log('errorMessage is credentials');
         return <Redirect to='/signin' />
        }
        if(!signedIn) {
            console.log('feed: signedin is true');
         return <Redirect to='/signin' />
        }
         

    return (
    <div>
        <div className='nav'>
          {signedIn ?
            <div>
                <Link to='/' className='links'>Home</Link>
                <Link to='/createarticle' className='links'>Create Article</Link>
                <Link to='/postgif' className='links'>Post GIF Images</Link>
                {/* <Link to='/profile' className='links'>Profile</Link> */}
                <SignOut onClick={signOut} />
            </div>:
            <div>
                <Link to='/signin' className='links'>Sign In</Link>
            </div>
          }
        </div>
        <div>
        {/* <CreateArticle /> */}


        {errorMessage ? <span className='error'>{(typeof errorMessage) !== 'string' ? errorMessage.message : errorMessage} </span> : <span></span>}
        </div>
            <div>
              <h3>Posts!</h3>
            </div>
        <div>{feed.data.length ?
            <div className='App'>{feed.data.map((item, index) => (
                <div key={index} className='card'>
                {item.editing ?
                  <div>
                    <span className='cancel btn' onClick={() => onCancel(item)}>
                    Cancel</span>
                    <span className='delete update btn' onClick={() => onUpdate({ id: item.id, title: this.state.title, article: this.state.article }) }>
                      Update</span><br />
                    <input type='text' name='title' placeholder={item.title} onChange={handleChange} /><br /> 
                    <textarea type='text' name='article' placeholder={item.articleOrUrl} onChange={handleChange} />
                  </div>
                  :
                  <div>
                    <span><b>{item.title}</b></span><br />
                    <span>{item.articleOrUrl.includes('http:') ? <img src={item.articleOrUrl} alt='gif post' /> : <span>{item.articleOrUrl} </span>}</span><br /><br />

                    <span>{!item.articleOrUrl.includes('http:') ?  
                    <span className='edit btn' onClick={() => onEdit(item)}>Edit</span>
                    :
                    <span></span>}
                    </span>
                    <span className='delete btn' onClick={() => onDelete(item)}>
                      Delete</span><br />
                  </div>
                  } </div>
            ))}</div>
            :
<div>No posts available{data}</div>
            }
            
            </div>
    </div>
    );
  }

export default Feed;
